import BaseApi from "./base.api";
export class ScrapApi extends BaseApi {
  constructor($axios) {
    super($axios);
    this.api.setBaseURL(`/api/scrap/`);
  }

  scrapSitemap(body) {
    return this.api.$post(`sitemap`, body);
  }

  addSitemap(body) {
    return this.api.$post(`add-sitemap`, body);
  }

  addDoctorFromSitemap(body) {
    return this.api.$post(`add-doctor-sitemap`, body);
  }

  operation(body) {
    return this.api.$post(`operation`, body);
  }
}
